/* ============================================
   СТИЛИ УРОКА "СТЕКЛЯННЫЕ КАРТОЧКИ"
   ============================================ */

.lesson-page {
  animation: fadeIn 0.5s ease-out;
  padding-bottom: 60px;
}

/* Заголовок урока */
.lesson-header {
  text-align: center;
  margin-bottom: 40px;
  position: relative;
}

.lesson-title {
  font-size: 2.8rem;
  margin-bottom: 20px;
  background: linear-gradient(to right, var(--accent-primary), var(--accent-secondary));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  animation: textGlow 3s infinite alternate;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

@keyframes textGlow {
  0% {
    text-shadow: 0 2px 10px rgba(0, 0, 0, 0.2);
  }
  100% {
    text-shadow: 0 2px 20px rgba(78, 205, 196, 0.3), 
                 0 4px 30px rgba(157, 80, 187, 0.2);
  }
}

.lesson-subtitle {
  font-size: 1.2rem;
  color: var(--text-secondary);
  max-width: 800px;
  margin: 0 auto 30px;
  line-height: 1.6;
}

/* Мета-информация */
.lesson-meta {
  display: flex;
  justify-content: center;
  gap: 25px;
  flex-wrap: wrap;
  margin-top: 25px;
}

.meta-item {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 10px 20px;
  border-radius: 50px;
  font-size: 0.95rem;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all var(--transition-medium);
}

.meta-item:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

/* Основной контент */
.lesson-content {
  padding: 40px;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.lesson-section {
  animation: slideUp 0.5s ease-out;
  animation-fill-mode: both;
}

.lesson-section:nth-child(1) { animation-delay: 0.1s; }
.lesson-section:nth-child(2) { animation-delay: 0.2s; }
.lesson-section:nth-child(3) { animation-delay: 0.3s; }
.lesson-section:nth-child(4) { animation-delay: 0.4s; }
.lesson-section:nth-child(5) { animation-delay: 0.5s; }
.lesson-section:nth-child(6) { animation-delay: 0.6s; }

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Заголовки секций */
.section-title {
  font-size: 1.8rem;
  margin-bottom: 25px;
  color: var(--accent-primary);
  display: flex;
  align-items: center;
  gap: 15px;
}

.section-text {
  font-size: 1.05rem;
  color: var(--text-secondary);
  line-height: 1.7;
  margin-bottom: 25px;
  max-width: 900px;
}

/* Текст введения */
.intro-text {
  font-size: 1.1rem;
  line-height: 1.8;
  color: var(--text-secondary);
}

/* Текст демонстрации */
.demo-description {
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--text-secondary);
  margin-bottom: 30px;
}

/* Кнопки */
.show-code-btn {
  margin-top: 15px;
  padding: 12px 25px;
  font-size: 1rem;
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  border: none;
  font-weight: 600;
}

.show-code-btn:hover {
  background: linear-gradient(135deg, var(--accent-secondary), var(--accent-primary));
}

/* ============================================
   ПРИНЦИПЫ КАК КАРТОЧКИ
   ============================================ */

.principles-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 25px;
  margin-top: 20px;
}

.principle-card {
  padding: 25px;
  display: flex;
  align-items: flex-start;
  gap: 20px;
  transition: all var(--transition-medium);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.principle-card:hover {
  transform: translateY(-5px);
  border-color: var(--accent-primary);
  box-shadow: var(--shadow-heavy);
}

.principle-number {
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  color: white;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  font-weight: bold;
  flex-shrink: 0;
}

.principle-content {
  flex: 1;
}

.principle-title {
  font-size: 1.2rem;
  color: var(--text-primary);
  margin-bottom: 10px;
  font-weight: 600;
}

.principle-description {
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
  font-size: 0.95rem;
}

/* ============================================
   АНИМИРОВАННЫЙ ФОН ДЛЯ ДЕМОНСТРАЦИИ
   ============================================ */

.demo-background {
  position: relative;
  border-radius: 16px;
  overflow: hidden;
  margin: 20px 0 30px;
  padding: 40px 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: linear-gradient(135deg, 
    rgba(26, 26, 46, 0.95),
    rgba(22, 33, 62, 0.95),
    rgba(15, 52, 96, 0.95));
}

.background-elements {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
  overflow: hidden;
}

.bg-circle {
  position: absolute;
  border-radius: 50%;
  filter: blur(40px);
  opacity: 0.5;
  animation: float 15s infinite ease-in-out;
}

.circle-1 {
  width: 250px;
  height: 250px;
  background: linear-gradient(135deg, #ff6b6b, #ffa726);
  top: 10%;
  left: 5%;
  animation-delay: 0s;
}

.circle-2 {
  width: 300px;
  height: 300px;
  background: linear-gradient(135deg, #4ecdc4, #44a08d);
  bottom: 10%;
  right: 10%;
  animation-delay: 5s;
}

.circle-3 {
  width: 200px;
  height: 200px;
  background: linear-gradient(135deg, #9d50bb, #6e48aa);
  top: 40%;
  left: 70%;
  animation-delay: 10s;
}

@keyframes float {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  33% {
    transform: translate(-20px, -30px) scale(1.05);
  }
  66% {
    transform: translate(15px, 20px) scale(0.95);
  }
}

.demo-content {
  position: relative;
  z-index: 2;
}

/* Демонстрация */
.demo-controls {
  padding: 25px;
  margin: 20px 0 30px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.controls-title {
  font-size: 1.4rem;
  margin-bottom: 25px;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 10px;
}

.controls-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
}

.control-group {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.control-group label {
  font-size: 1rem;
  color: var(--text-secondary);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.control-value {
  color: var(--accent-primary);
  font-weight: 600;
  font-size: 1.1rem;
  min-width: 50px;
  text-align: right;
}

input[type="range"] {
  width: 100%;
  height: 8px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.1);
  outline: none;
  -webkit-appearance: none;
  transition: all var(--transition-medium);
}

input[type="range"]:hover {
  background: rgba(255, 255, 255, 0.15);
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: var(--accent-primary);
  cursor: pointer;
  box-shadow: 0 0 10px rgba(78, 205, 196, 0.5);
  transition: all var(--transition-fast);
  border: 2px solid rgba(255, 255, 255, 0.3);
}

input[type="range"]::-webkit-slider-thumb:hover {
  transform: scale(1.2);
  box-shadow: 0 0 15px rgba(78, 205, 196, 0.8);
}

.range-labels {
  display: flex;
  justify-content: space-between;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
}

/* Карточки демо */
.demo-cards {
  margin-top: 30px;
}

.cards-title {
  font-size: 1.4rem;
  margin-bottom: 25px;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 10px;
}

.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 25px;
}

.demo-card-wrapper {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.demo-card {
  padding: 25px;
  min-height: 220px;
  display: flex;
  flex-direction: column;
  transition: all var(--transition-medium);
  position: relative;
  overflow: hidden;
}

.demo-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.7s ease;
}

.demo-card:hover::before {
  left: 100%;
}

.demo-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.card-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
}

.card-icon {
  font-size: 1.8rem;
  color: var(--accent-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: rgba(78, 205, 196, 0.1);
  border-radius: 12px;
  flex-shrink: 0;
}

.card-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.card-description {
  color: rgba(255, 255, 255, 0.85);
  line-height: 1.6;
  margin-bottom: 20px;
  flex-grow: 1;
}

.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  flex-wrap: wrap;
  gap: 10px;
}

.blur-value {
  color: var(--accent-primary);
  font-weight: 600;
  font-size: 0.95rem;
}

.card-tag {
  background: rgba(255, 255, 255, 0.1);
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  color: var(--accent-primary);
  border: 1px solid rgba(78, 205, 196, 0.2);
  transition: all var(--transition-medium);
}

.demo-card:hover .card-tag {
  background: rgba(255, 255, 255, 0.15);
  transform: scale(1.05);
}

.get-code-btn {
  align-self: flex-start;
  padding: 10px 20px;
  font-size: 0.95rem;
}

/* Преимущества */
.benefits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
  margin-top: 20px;
}

.benefit-card {
  padding: 25px;
  display: flex;
  align-items: flex-start;
  gap: 20px;
  transition: all var(--transition-medium);
}

.benefit-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-heavy);
}

.benefit-icon {
  width: 45px;
  height: 45px;
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  font-weight: bold;
  flex-shrink: 0;
}

.benefit-text {
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
  flex: 1;
}

/* Примеры кода */
.code-examples {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  margin-top: 20px;
}

.code-example {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  padding: 25px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
}

.code-example h3 {
  font-size: 1.3rem;
  margin-bottom: 20px;
  color: var(--text-primary);
}

.code-block {
  background: rgba(0, 0, 0, 0.4);
  border-radius: 8px;
  padding: 20px;
  overflow-x: auto;
  margin-bottom: 20px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  line-height: 1.6;
  color: var(--accent-primary);
  flex-grow: 1;
}

.code-actions {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.glass-button.small {
  padding: 8px 16px;
  font-size: 0.9rem;
  height: auto;
  min-width: auto;
}

/* Практическое задание */
.challenge-section {
  background: linear-gradient(135deg, rgba(78, 205, 196, 0.05), rgba(157, 80, 187, 0.05));
  border-radius: 16px;
  padding: 30px;
  border: 1px solid rgba(78, 205, 196, 0.1);
}

.challenge-content {
  max-width: 800px;
}

.challenge-description {
  font-size: 1.1rem;
  color: var(--text-secondary);
  margin-bottom: 25px;
  line-height: 1.6;
}

.challenge-requirements {
  margin: 25px 0;
  padding-left: 25px;
  color: var(--text-secondary);
  list-style: none;
}

.challenge-requirements li {
  margin-bottom: 12px;
  line-height: 1.6;
  position: relative;
  padding-left: 30px;
}

.challenge-requirements li:before {
  content: '✓';
  position: absolute;
  left: 0;
  color: var(--accent-primary);
  font-weight: bold;
  font-size: 1.1rem;
}

.challenge-actions {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 30px;
}

.challenge-btn {
  padding: 12px 25px;
  background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
  border: none;
  font-weight: 600;
}

.hint-btn {
  padding: 12px 25px;
  background: linear-gradient(135deg, var(--accent-warning), var(--accent-error));
  border: none;
  font-weight: 600;
}

/* ============================================
   БОЛЬШИЕ КАРТОЧКИ СОВЕТОВ
   ============================================ */

.tips-section {
  background: linear-gradient(135deg, rgba(38, 162, 105, 0.05), rgba(78, 205, 196, 0.05));
  border-radius: 16px;
  padding: 30px;
  border: 1px solid rgba(38, 162, 105, 0.1);
}

.tip-text {
  font-size: 1.1rem;
  color: var(--text-secondary);
  margin-bottom: 30px;
  line-height: 1.6;
  max-width: 800px;
}

.tips-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 25px;
}

.tip-card-large {
  padding: 25px;
  transition: all var(--transition-medium);
  min-height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.tip-card-large:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-heavy);
  border-color: var(--accent-success);
}

.tip-card-large h4 {
  font-size: 1.2rem;
  color: var(--accent-success);
  margin-bottom: 15px;
  font-weight: 600;
}

.tip-card-large p {
  color: var(--text-secondary);
  font-size: 1rem;
  line-height: 1.6;
  margin: 0;
}

/* ============================================
   МОБИЛЬНАЯ ОПТИМИЗАЦИЯ
   ============================================ */

