  .lesson-page {
    padding: 0 0 40px;
  }
  
  .lesson-header {
    padding: 20px 15px;
    margin-bottom: 20px;
  }
  
  .lesson-title {
    font-size: 2rem;
    flex-direction: column;
    text-align: center;
    gap: 10px;
  }
  
  .lesson-subtitle {
    font-size: 1rem;
    padding: 0 10px;
    line-height: 1.5;
  }
  
  .lesson-meta {
    gap: 8px;
    padding: 0 10px;
    margin-top: 20px;
  }
  
  .meta-item {
    padding: 8px 12px;
    font-size: 0.8rem;
    gap: 8px;
    border-radius: 12px;
  }
  
  .lesson-content {
    padding: 15px;
    gap: 40px;
    border-radius: 12px;
