    width: 150px;
    height: 150px;
  }
  
  /* Панель управления - вертикальный стек */
  .demo-controls {
    padding: 15px;
    margin: 15px 0;
    border-radius: 12px;
  }
  
  .controls-title {
    font-size: 1.2rem;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .controls-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .control-group {
    gap: 10px;
  }
  
  .control-group label {
    font-size: 0.95rem;
  }
  
  .control-value {
    font-size: 1rem;
  }
  
  input[type="range"] {
    height: 10px;
  }
  
  input[type="range"]::-webkit-slider-thumb {
    width: 24px;
    height: 24px;
  }
  
  .range-labels {
    font-size: 0.8rem;
  }
  
  /* Карточки - одна колонка с увеличенными отступами */
  .demo-cards {
    margin-top: 25px;
  }
  
  .cards-title {
    font-size: 1.2rem;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .cards-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .demo-card-wrapper {
    gap: 12px;
  }
  
  .demo-card {
    padding: 20px;
    min-height: 200px;
  }
  
  .card-header {
    gap: 12px;
    margin-bottom: 12px;
  }
  
  .card-icon {
    width: 40px;
    height: 40px;
    font-size: 1.4rem;
    border-radius: 10px;
  }
  
  .card-title {
    font-size: 1.2rem;
  }
  
  .card-description {
    font-size: 0.9rem;
    margin-bottom: 15px;
  }
  
  .card-footer {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .blur-value, .card-tag {
    font-size: 0.85rem;
  }
  
  .get-code-btn {
    width: 100%;
    justify-content: center;
    padding: 12px;
    font-size: 0.95rem;
  }
  
  /* Преимущества - одна колонка */
  .benefits-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .benefit-card {
    padding: 20px;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 15px;
  }
  
  .benefit-icon {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }
  
  .benefit-text {
    font-size: 0.9rem;
  }
  
  /* Примеры кода - вертикальный стек */
  .code-examples {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .code-example {
    padding: 20px;
  }
  
  .code-example h3 {
    font-size: 1.1rem;
    margin-bottom: 15px;
  }
  
  .code-block {
    padding: 15px;
    font-size: 0.85rem;
    margin-bottom: 15px;
  }
  
  .code-actions {
    flex-direction: column;
    gap: 10px;
  }
  
  .glass-button.small {
    width: 100%;
    padding: 10px;
    font-size: 0.9rem;
  }
  
  /* Практическое задание */
  .challenge-section {
    padding: 20px;
    border-radius: 12px;
  }
  
  .challenge-description {
    font-size: 1rem;
    margin-bottom: 20px;
  }
  
  .challenge-requirements {
    padding-left: 20px;
    margin: 20px 0;
  }
  
  .challenge-requirements li {
    font-size: 0.9rem;
    margin-bottom: 10px;
    padding-left: 25px;
  }
  
  .challenge-actions {
    flex-direction: column;
    gap: 10px;
    margin-top: 20px;
  }
  
  .challenge-btn, .hint-btn {
    width: 100%;
    padding: 12px;
    font-size: 0.95rem;
  }
  
  /* Советы - большие карточки */
  .tips-section {
    padding: 20px;
    border-radius: 12px;
  }
  
  .tip-text {
    font-size: 1rem;
    margin-bottom: 20px;
  }
  
  .tips-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .tip-card-large {
    padding: 20px;
    min-height: 160px;
  }
  
  .tip-card-large h4 {
    font-size: 1.1rem;
    margin-bottom: 10px;
  }
  
  .tip-card-large p {
    font-size: 0.95rem;
    line-height: 1.5;
  }
  
  /* Кнопки урока */
  .show-code-btn {
    width: 100%;
    padding: 14px;
    font-size: 0.95rem;
    margin-top: 20px;
  }
}

/* Для очень маленьких экранов */
@media (max-width: 480px) {
  .lesson-title {
    font-size: 1.7rem;
  }
  
  .lesson-subtitle {
    font-size: 0.95rem;
  }
  
  .meta-item span:last-child {
    display: none;
  }
  
  .controls-title, .cards-title {
    font-size: 1.1rem;
  }
  
  .demo-card {
    padding: 15px;
  }
  
  .card-description {
    font-size: 0.85rem;
  }
  
  input[type="range"]::-webkit-slider-thumb {
    width: 20px;
    height: 20px;
  }
  
  .get-code-btn {
    font-size: 0.9rem;
    padding: 10px;
  }
  
  /* Улучшение касаний для мобильных */
  button, 
  input[type="range"], 
  .meta-item,
  .demo-card,
  .benefit-card,
  .tip-card-large,
  .principle-card {
    touch-action: manipulation;
  }
  
  /* Увеличение минимальной высоты для касаний */
  button, 
  .get-code-btn, 
  .glass-button {
    min-height: 44px;
  }
  
  /* Улучшение отступов для удобного скролла */
  .lesson-section {
    scroll-margin-top: 20px;
  }
}

/* Для горизонтальной ориентации */
@media (max-width: 768px) and (orientation: landscape) {
  .principles-grid,
  .cards-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .controls-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .tips-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .code-modal .modal-body pre {
    max-height: 30vh;
  }
}

/* Поддержка темной темы для мобильных */
@media (prefers-color-scheme: dark) and (max-width: 768px) {
  .demo-background {
    background: linear-gradient(135deg, 
      rgba(13, 17, 23, 0.95),
      rgba(22, 27, 34, 0.95),
      rgba(33, 38, 45, 0.95));
  }
  
  .demo-card {
    border-color: rgba(255, 255, 255, 0.15);
  }
  
  .principle-card {
    border-color: rgba(255, 255, 255, 0.1);
  }
}